#main-nav {
  position: fixed;
  top: 0;
  z-index: 1;
  padding: 0 20px 7px 20px;
  width: 100vw;
  background-color: rgba(35, 39, 42, 0.8);
  display: flex; }
  #main-nav .flex-grow {
    flex-grow: 1; }
  #main-nav ul {
    list-style: none;
    display: flex;
    padding: 0px;
    margin: 0px; }
  #main-nav #nav-links li {
    display: inline-block;
    margin: 0px;
    padding: 0px; }
    #main-nav #nav-links li a {
      display: inline-block;
      text-decoration: none;
      background: #23272a;
      color: #86c332;
      padding: 10px 20px;
      margin-right: 7px;
      border-right: 2px solid #86c332;
      border-left: 2px solid #86c332;
      border-bottom: 2px solid #86c332; }
    #main-nav #nav-links li a:hover, #main-nav #nav-links li a:focus {
      background-color: #60892f;
      color: white;
      box-shadow: inset 1px 1px 5px #23272a; }
    #main-nav #nav-links li #a-welcome {
      color: white;
      font-weight: bold; }
  #main-nav ul#connect li {
    padding: 5px 10px; }
  #main-nav ul#connect a {
    display: inline-block;
    text-decoration: none;
    font-size: 2em;
    color: #86c332; }
  #main-nav ul#connect a:hover {
    color: #6b6e70; }

@media only screen and (max-width: 775px) {
  #main-nav {
    border-bottom: 1px solid #60892f;
    width: 100vh;
    height: 25px;
    right: 100vw;
    transform: rotate(-90deg);
    transform-origin: 100% 0; }
    #main-nav ul {
      flex-flow: row-reverse nowrap;
      justify-items: flex-end; }
    #main-nav a {
      position: relative;
      top: -5px; }
    #main-nav a:hover, #main-nav a:focus {
      padding-top: 20px;
      top: 0px; }
  ul#connect {
    display: block;
    position: absolute;
    top: calc(100vw - 130px);
    right: -1px;
    background-color: #23272a;
    border-radius: 5px 0 0 0; }
    ul#connect li {
      transform: rotate(90deg);
      padding: 5px;
      padding-top: 6px; }
    ul#connect a {
      top: 0; }
    ul#connect a:hover, ul#connect a:focus {
      padding-top: 0; }
  header, section, footer {
    padding-left: 40px !important; } }

